import styled from 'styled-components';

const style = {};

style.Root = styled.div`
   display: flex;
   flex-direction: column;
   padding: 16px 0;
`;

style.Header = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px 0;
`;

style.Title = styled.div`
  font-size: 16px;
  color: ${props => props.theme.colors.dark};
  width: 100%;
`;

style.RemoveCondition = styled.img`
  width: 24px;
  height: 24px;
  display: flex;
  flex-shrink: 0;
  cursor: pointer;
`;

style.ControlContainer = styled.div`
  padding: 8px 0;
`;

export default style;



