import styled, { css } from 'styled-components';

const style = {};

style.Root = styled.li`
    display: flex;
    flex-shrink: 0;
    align-items: start;
    flex-direction: column;
    cursor: pointer;
    padding: 0 8px 0 16px;
    overflow: hidden;
`;

style.Title = styled.div`
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
`;

style.AnswersRoot = styled.ul`
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  padding: 0;
`;

style.AnswerItem = styled.li`
  text-overflow: ellipsis;
  font-size: 14px;
  line-height: 21px;
  height: 40px;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  padding: 8px 0;
  background-color: ${props => props.theme.colors.white};
  color: ${props => props.theme.colors.cornflowerBlue};
  border-bottom: 1px solid ${props => props.theme.colors.mercuryHex};
  
  ${props => props.isFirst && css`
    border-top: 1px solid ${props.theme.colors.mercuryHex};
  `}
  
  &:hover {
    background-color: rgba(34,49,63,0.05);
  }
`;

export default style;



