import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const UlComponent = styled.ul`
  list-style: none;
  margin: 0;
  padding: 0;
`;

const UIList = (props) => {
  const {
    className,
    style,
    children,
  } = props;

  return (
    <UlComponent
      className={className}
      style={style}
    >
      {children}
    </UlComponent>
  );
};

UIList.defaultProps = {
  className: '',
  style: {},
};

UIList.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
  className: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.string,
  ]),
  style: PropTypes.object,
};

export default UIList;



