import React from 'react';
import PropTypes from 'prop-types';

import * as s from './button.style';

const Button = (props) => {
  const {
    color,
    size,
    link,
    disabled,
    children,
    fullWidth,
    ...other
  } = props;

  return (
    <s.Button
      href={link}
      green={color === 'green'}
      blue={color === 'blue'}
      large={size === 'large'}
      fullWidth={fullWidth}
      disabled={disabled}
      {...other}
    >
      <s.Text default={color !== 'green' && color !== 'blue'}>{children}</s.Text>
    </s.Button>
  );
};

Button.propTypes = {
  link: PropTypes.string.isRequired,

  color: PropTypes.oneOf(['default', 'green', 'blue']),

  size: PropTypes.oneOf(['default', 'large']),

  fullWidth: PropTypes.bool,

  disabled: PropTypes.bool,

  children: PropTypes.node.isRequired,
};

Button.defaultProps = {
  color: 'default',

  size: 'default',

  fullWidth: false,

  disabled: false,
};

export default Button;



