import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import Popover from './popover';

class UIPopover extends PureComponent {

  static propTypes = {
    style: PropTypes.object,
    popoverProps: PropTypes.object,
    initialLabel: PropTypes.object,
    children: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.node,
    ]),
    useAnchorWidth: PropTypes.bool,
    onRequestClose: PropTypes.func,
    useLayerForClickAway: PropTypes.bool,
    anchorOrigin: PropTypes.object,
  };

  static defaultProps = {
    style: null,
    popoverProps: {},
    initialLabel: {},
    children: null,
    useAnchorWidth: false,
    onRequestClose: () => {},
    useLayerForClickAway: false,
    anchorOrigin: {},
  };

  constructor(props) {
    super(props);

    this.state = {
      open: props.popoverProps.showing,
      anchorEl: props.popoverProps.anchorEl,
    };
  }

  componentWillUpdate(nextProps) {
    const {
      props: {
        popoverProps,
      },
    } = this;

    if (popoverProps.showing !== nextProps.popoverProps.showing) {
      this.handleTouchTap({
        open: nextProps.popoverProps.showing,
        anchorEl: nextProps.popoverProps.anchorEl,
      });
    }
  }

  handleTouchTap = (data) => {
    this.setState({
      open: data.open,
      anchorEl: data.anchorEl,
    });
  };

  render() {
    const {
      props: {
        children,
        useAnchorWidth,
        onRequestClose,
        useLayerForClickAway,
        anchorOrigin,
        style,
      },
    } = this;

    return (
      <div
        style={style}
      >
        <div>{this.props.initialLabel}</div>
        <Popover
          open={this.state.open}
          anchorEl={this.state.anchorEl}
          anchorOrigin={{
            horizontal: 'left',
            vertical: anchorOrigin && anchorOrigin.vertical ? anchorOrigin.vertical : 'bottom',
          }}
          targetOrigin={{ horizontal: 'left', vertical: 'top' }}
          useAnchorWidth={useAnchorWidth}
          onRequestClose={onRequestClose}
          useLayerForClickAway={useLayerForClickAway}
        >
          <div>
            {children}
          </div>
        </Popover>
      </div>
    );
  }

}

export default UIPopover;



