import React from 'react';

import ArrowBack from '../icons/arrowBack.svg';
import s from './button.style';

const Button = () => (
  <s.Button
    href="#/"
  >
    <img src={ArrowBack} alt="back" />
  </s.Button>
);

export default Button;



