import React, { PureComponent } from 'react';
import LinkButton from '../_components/linkButton';

import s from './root.style';

export class Root extends PureComponent {

  render() {
    return (
      <s.Root>
        <s.ControlContainer>
          <LinkButton
            color="blue"
            link="#/addField"
          > Add Field
          </LinkButton>
        </s.ControlContainer>
        <s.ControlContainer>
          <LinkButton
            color="blue"
            link="#/fields"
          > Fields
          </LinkButton>
        </s.ControlContainer>
        <s.ControlContainer>
          <LinkButton
            color="blue"
            link="#/addCondition"
          > Add Condition
          </LinkButton>
        </s.ControlContainer>
        <s.ControlContainer>
          <LinkButton
            color="blue"
            link="#/questions"
          > Questions
          </LinkButton>
        </s.ControlContainer>
      </s.Root>
    );
  }

}

export default Root;



