export class Adapter {

  constructor() {
    this.answers = {};
    this.questions = [];
  }

  getQuestions = async () => {
    await window.Word.run(async (context) => {
      const { body } = context.document;
      context.load(body);
      await context.sync().then(async () => {
        const { text } = body;
        const found = [];
        const rxp = /\[([^\]]+)\]/g;
        let curMatch;
        // eslint-disable-next-line
        while (curMatch = rxp.exec(text)) {
          found.push(curMatch[1]);
        }

        found.forEach((item) => {
          const textCondition = item.split(/[|[\]]+/)[0];
          const conditions = textCondition.split(';');
          this.getQuestionsFromArray(conditions);
        });

        await context.sync();
      });
    });
  };

  getQuestionsFromArray(conditions) {
    conditions.forEach((q) => {
      const model = {};
      [model.question, model.answer] = q.split(':');

      const hasQuestion = this.questions.includes(model.question);
      if (!hasQuestion) {
        this.questions.push(model.question);
        this.answers[model.question] = [];
        this.answers[model.question].push(model.answer);
      } else {
        const hasAnswer = this.answers[model.question].includes(model.answer);
        if (!hasAnswer) {
          this.answers[model.question].push(model.answer);
        }
      }
    });
  }

  saveQuestion = async (oldQuestion, newQuestion) => {
    await window.Word.run(async (context) => {
      const { body } = context.document;
      const searchResultsQuestions = body.search(`${oldQuestion}:`, { matchWildCards: true });
      context.load(searchResultsQuestions);

      await context.sync().then(async () => {
        searchResultsQuestions.items.forEach((item) => {
          item.insertText(`${newQuestion}:`, window.Word.InsertLocation.replace);
        });

        await context.sync();
      });
    });
  };

  saveAnswer = async (question, oldAnswer, newAnswer) => {
    await window.Word.run(async (context) => {
      const { body } = context.document;
      const searchResultsQuestions = body
        .search(`${question}:${oldAnswer}`, { matchWildCards: true });
      context.load(searchResultsQuestions);

      await context.sync().then(async () => {
        searchResultsQuestions.items.forEach((item) => {
          item.insertText(`${question}:${newAnswer}`, window.Word.InsertLocation.replace);
        });

        await context.sync();
      });
    });
  };

}

export default Adapter;



