import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

import Suggest from '../_components/suggest';
import CloseIcon from '../_components/icons/closeCross.svg';

import s from './item.style';

export class Item extends PureComponent {

  static propTypes = {
    item: PropTypes.object.isRequired,
    questions: PropTypes.object.isRequired,
    onChange: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
  };

  getQuestions = () => Object.keys(this.props.questions);

  getAnswers = () => {
    const { question } = this.props.item;
    if (this.props.questions[question]) {
      return Object.keys(this.props.questions[question]);
    }

    return [];
  };

  handleChangeQuestion = (option) => {
    this.props.onChange({
      ...this.props.item,
      question: option,
    });
  };

  handleChangeAnswer = (option) => {
    this.props.onChange({
      ...this.props.item,
      answer: option,
    });
  };

  handleDelete = () => {
    const { id } = this.props.item;
    this.props.onDelete(id);
  };

  handleGetValue = item => item;

  render() {
    return (
      <s.Root>
        <s.Header>
          <s.Title>Question</s.Title>
          <s.RemoveCondition
            onClick={this.handleDelete}
            src={CloseIcon}
            alt="close"
          />
        </s.Header>
        <s.ControlContainer>
          <Suggest
            item={this.props.item.question}
            items={this.getQuestions()}
            onChange={this.handleChangeQuestion}
            onNew={this.handleChangeQuestion}
            getKey={this.handleGetValue}
            getSuggestionValue={this.handleGetValue}
            label="Title"
          />
        </s.ControlContainer>
        <s.ControlContainer>
          <Suggest
            item={this.props.item.answer}
            items={this.getAnswers()}
            onChange={this.handleChangeAnswer}
            onNew={this.handleChangeAnswer}
            getKey={this.handleGetValue}
            getSuggestionValue={this.handleGetValue}
            label="Answer"
          />
        </s.ControlContainer>
      </s.Root>
    );
  }

}

export default Item;



