import React, { Component } from 'react';
import { Router } from 'react-router-dom/es';

import { Routes } from './router';
import { history } from './router/history';

class App extends Component {

  render() {
    return (
      <Router history={history}>
        {Routes}
      </Router>
    );
  }

}

export default App;



