import styled, { css } from 'styled-components';

const style = {};

style.Root = styled.div`
  position: relative;
  width: 100%;
  font-size: 13px;
  line-height: 16px;
`;

style.Label = styled.label`
  position: absolute;
  top: 16px;
  left: 16px;
  color: ${props => props.theme.colors.stone};
  transform: translate(0, 1px) scale(1);
  transform-origin: top left;
  transition: all 150ms cubic-bezier(0.23, 1, 0.32, 1);
  pointer-events: none;
  
  ${props => props.shrink && css`
    color: ${props.theme.colors.cornflowerBlue};
    transform: translate(0, -7px) scale(0.85);
  `}

  ${props => props.disabled && css`
    color: ${props.theme.colors.silver};
  `}
  
  ${props => props.error && css`
    color: ${props.theme.colors.brightOrange};
  `}
`;

style.LabelText = styled.div`
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

style.Input = styled.input`
  box-sizing: border-box;
  height: 47px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
  padding: 24px 16px 7px;
  border: 1px solid ${props => props.theme.colors.mercuryHex};
  font: inherit;
  background-color: ${props => props.theme.colors.white};

  &:hover,
  &:focus {
    background-color: ${props => props.theme.colors.paleGrey};
  }

  &:active {
    background-color: ${props => props.theme.colors.duckEggBlue};
  }
  
  ${props => props.disabled && css`
    background-color: ${props.theme.colors.white};
  `}

  ${props => props.error && css`
    background-color: ${props.theme.colors.veryLightPink};
  `}

  &:focus {
    outline: none;
  }
`;

style.Tooltip = styled.div`
  position: absolute;
  z-index: 5;
  bottom: 100%;
  max-width: 100%;
  padding: 6px 9px;
  background-color: ${props => props.theme.colors.brightOrange};
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  
  &:before {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    margin: -1px 0 0 -6px;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-top: 8px solid ${props => props.theme.colors.brightOrange};
  }
`;

style.TooltipContainer = styled.div`
  font-size: 11px/1.45;
  line-height: 1.45;
  color: ${props => props.theme.colors.white};
`;

export default style;



