export class Adapter {

  constructor() {
    this.questions = {};
  }

  getQuestions = async () => {
    await window.Word.run(async (context) => {
      const { body } = context.document;
      const searchResultsQuestions = body.search('[[]*[]]', { matchWildCards: true });
      context.load(searchResultsQuestions);
      await context.sync().then(async () => {
        searchResultsQuestions.items.forEach((item) => {
          const text = item.text.split(/[|[\]]+/).slice(1, -1)[0];
          const conditions = text.split(';');
          this.getQuestionsFromArray(conditions);
        });

        await context.sync();
      });
    });
  };

  getQuestionsFromArray(conditions) {
    conditions.forEach((q) => {
      const model = {};
      [model.question, model.answer] = q.split(':');
      this.questions[model.question] = this.questions[model.question] ? this.questions[model.question] : {};
      this.questions[model.question][model.answer] = this.questions[model.question][model.answer] ?
        this.questions[model.question][model.answer] :
        {};
    });
  }

  addCondition = async (condition) => {
    const conditionLength = condition.length;
    let conditionStart = '[';
    const conditionEnd = ']';
    condition.forEach((c, index) => {
      if (index === conditionLength - 1) {
        conditionStart = `${conditionStart}${c.question}:${c.answer}|`;
      } else {
        conditionStart = `${conditionStart}${c.question}:${c.answer};`;
      }
    });

    await window.Word.run(async (context) => {
      const range = context.document.getSelection();
      range.insertText(conditionStart, window.Word.InsertLocation.start);
      range.insertText(conditionEnd, window.Word.InsertLocation.end);
      const searchStartResult = range.search('[[]*[|]', { matchWildCards: true });
      const searchEndResult = range.search('[]]', { matchWildCards: true });
      context.load(searchStartResult);
      context.load(searchEndResult);

      await context.sync().then(async () => {
        searchStartResult.items[0].font.color = 'red';
        searchStartResult.items[0].font.size = 10;
        searchEndResult.items[0].font.color = 'red';
        searchEndResult.items[0].font.size = 10;
      });
    });
  };

}

export default Adapter;



