import React, { Component } from 'react';
import { ThemeProvider } from 'styled-components';

import theme from './ui/theme/default';
import s from './root.style';

import App from './app';

class Root extends Component {

  render() {
    return (
      <s.Root>
        <ThemeProvider theme={theme}>
          <App />
        </ThemeProvider>
      </s.Root>
    );
  }

}

export default Root;



