import styled, { css } from 'styled-components';

const style = {};

style.DropdownWrapper = styled.div`
  width: 100%;
  height: 47px;
  border: 1px solid ${props => props.theme.colors.mercuryHex};
  border-right: 1px solid ${props => props.theme.colors.mercuryHex};
  border-left: 1px solid ${props => props.theme.colors.mercuryHex};
  border-top: 1px solid ${props => props.theme.colors.mercuryHex};
  background: ${props => props.theme.colors.white};
  
  
  ${props => props.noRightBorder && css`
    border-right: none;
  `}
  
  ${props => props.noLeftBorder && css`
    border-left: none;
  `};
  
  ${props => props.noTopBorder && css`
    border-top: none;
  `};
  
  ${props => props.active && css`
    background: ${props.theme.colors.paleGrey};
  `};
`;

style.DropdownButton = styled.div`
  display: flex;
  flex-direction: row;
  height: 47px;
  width: 100%;
  position: relative;
  cursor: pointer;
  outline: none;
  
  ${props => props.disabled && css`
    cursor: not-allowed;
  `}
`;

style.ListItem = styled.div`
  font-size: 13px;
  height: 40px;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  cursor: pointer;
  color: ${props => props.theme.colors.dark};
  background: ${props => props.theme.colors.white};
  
  ${props => props.active && css`
    color: ${props.theme.colors.white};
    background: ${props.theme.colors.cornflowerBlue};
  `}
  
  ${props => props.disableItemCapitalizing && css`
    text-transform: none;
  `}
`;

style.DropdownPlaceholder = styled.div`
  position: absolute;
  display: block;
  left: 1px;
  top: 16px;
  padding: 0 16px;
  color: ${props => props.theme.colors.cornflowerBlue};
  transition: all 150ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  pointer-events: none;
  user-select: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 10px;
  z-index: 0;
  transform: scale(1) translate3d(0px, -6px, 0px);
  transform-origin: left top 0;
  
  ${props => props.disabled && css`
    color: ${props.theme.colors.silver}
  `}
`;

style.DropdownValue = styled.div`
  outline: none;
  font-size: 13px;
  margin: 0;
  border-radius: 0;
  overflow: hidden;
  resize: none;
  padding: 25px 16px 11px 16px;
  color: ${props => props.theme.colors.dark};
  border: none;
  display: block;
  box-sizing: border-box;
  transition: box-shadow 0.2s ease-out;
  outline: 0;
  line-height: initial;
  box-shadow: transparent 0 0 0 2px;
  height: 47px;
  text-overflow: ellipsis;
  text-transform: capitalize;

  &:focus {
    background: rgba(177, 210, 252, 0.15);
  }
`;

style.DropdownListWrapper = styled.div`
  background: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.25) 0 4px 8px
`;

style.DropdownInput = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
`;

style.DropdownArrow = styled.div`
  user-select: none;
  height: 49px;
  width: 49px;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
`;

style.ItemName = styled.div`
  padding: 0 16px;
`;

export default style;



