import React from 'react';

import {
  Route,
  Switch,
} from 'react-router-dom/es';

import RoutePath from './path';

import { Root } from '../ui/root/root';
import { AddField } from '../ui/addField/addField';
import { EditField } from '../ui/editField/editField';
import { Fields } from '../ui/fields/fields';
import { AddCondition } from '../ui/addCondition/addCondition';
import { Questions } from '../ui/questions/questions';

const Routes = (
  <Switch>
    <Route
      exact
      path={RoutePath.Root}
      component={Root}
    />
    <Route
      exact
      path={RoutePath.AddField}
      component={AddField}
    />
    <Route
      exact
      path={RoutePath.EditField}
      component={EditField}
    />
    <Route
      exact
      path={RoutePath.Fields}
      component={Fields}
    />
    <Route
      exact
      path={RoutePath.AddCondition}
      component={AddCondition}
    />
    <Route
      exact
      path={RoutePath.Questions}
      component={Questions}
    />
  </Switch>
);

export default Routes;



