export class Validator {

  constructor() {
    this.isValid = true;
    this.validateMessage = '';
  }

  get canInsert() {
    return this.isValid;
  }

  get message() {
    return this.validateMessage;
  }

  validateField = async () => {
    await this._checkInsideTable();
    await this._checkForBadSymbols();
    await this._mainChecker();
  };

  validateCondition = async () => {
    await this._checkSelectionCollapsed();
    await this.validateField();
    await this._imageChecker();
  };

  _checkSelectionCollapsed = async () => {
    await window.Word.run(async (context) => {
      window.Office.context.document.getSelectedDataAsync(window.Office.CoercionType.Text, async (asyncResult) => {
        if (asyncResult.status !== window.Office.AsyncResultStatus.Failed) {
          if (!asyncResult.value) {
            this.isValid = false;
            this.validateMessage = 'Selection is collapsed';
          }
        }
      });

      await context.sync();
    });
  };

  _checkForBadSymbols = async () => {
    if (this.isValid) {
      await window.Word.run(async (context) => {
        const range = context.document.getSelection();
        const fieldSymbols = range.search('[{}]', { matchWildCards: true });
        fieldSymbols.load();
        const conditionStartSymbols = range.search('[[*|]', { matchWildCards: true, ignoreSpace: true });
        conditionStartSymbols.load();
        const conditionEndSymbol = range.search('[]]', { matchWildCards: true, ignoreSpace: true });
        conditionEndSymbol.load();
        await context.sync().then(async () => {
          if (fieldSymbols.items.length) {
            this.isValid = false;
            this.validateMessage = 'Selection contains field symbols';
          }

          const trueBadSymbols = conditionStartSymbols.items.filter(item => item.text !== '.');
          if (trueBadSymbols.length) {
            this.isValid = false;
            this.validateMessage = 'Selection contains condition symbols';
          }

          if (conditionEndSymbol.items.length) {
            this.isValid = false;
            this.validateMessage = 'Selection contains condition symbols';
          }
        });
      });
    }
  };

  _checkInsideTable = async () => {
    await window.Word.run(async (context) => {
      window.Office.context.document.getSelectedDataAsync(window.Office.CoercionType.Matrix, async (asyncResult) => {
        if (asyncResult.status !== window.Office.AsyncResultStatus.Failed) {
          if (asyncResult.value.length > 1) {
            this.isValid = false;
            this.validateMessage = `Selection contains ${asyncResult.value.length} rows`;
          }

          // Multiple cell select
          if (asyncResult.value[0].length > 1) {
            this.isValid = false;
            this.validateMessage = `Selection contains ${asyncResult.value[0].length} cells`;
          }
        }
      });

      await context.sync();
    });
  };

  _mainChecker = async () => {
    if (this.isValid) {
      await window.Word.run(async (context) => {
        const range = context.document.getSelection();
        range.load('text');
        await context.sync();
        if (range.text.length) {
          const html = range.getHtml();
          await context.sync();
          this._domParse(html.value);
        }
      });
    }
  };

  _imageChecker = async () => {
    if (this.isValid) {
      await window.Word.run(async (context) => {
        const range = context.document.getSelection();
        range.load('text');
        await context.sync();
        if (range.text.length) {
          const html = range.getHtml();
          await context.sync();
          if (html.value.includes('<img')) {
            this.isValid = false;
            this.validateMessage = 'Selection contains image block';
          }
        } else {
          this.isValid = false;
          this.validateMessage = 'Selection is collapsed';
        }
      });
    }
  };

  _domParse(html) {
    const parser = new DOMParser();
    const dom = parser.parseFromString(html, 'text/html');
    const nodes = dom.getElementsByTagName('body')[0].children[0].children;
    const nodesList = Array.prototype.slice.call(nodes).filter((n) => {
      const textLength = n.innerText.length === 1;
      // condition for check &nbsp;
      if (textLength) {
        // check &nbsp;
        const hasNbsp = n.innerText.charCodeAt(0) === 32 || n.innerText.charCodeAt(0) === 160;
        return !hasNbsp;
      }

      return true;
    });

    const first = nodesList[0];
    const last = nodesList[nodesList.length - 1];

    if (first.className === 'MsoNormal') {
      this._checkParagraphs(first, last);
    }

    if (first.className.toLowerCase().includes('list')) {
      this._checkList(nodesList, last);
    }

    if (first.className.toLowerCase().includes('table')) {
      this._checkTables(nodes, last);
    }
  }

  _checkParagraphs(first, last) {
    if (first.className !== last.className) {
      this.isValid = false;
      this.validateMessage = 'Selection contains different blocks';
    }
  }

  _checkList(nodes, last) {
    if (last.className === 'MsoNormal' || last.className.toLowerCase().includes('table')) {
      this.isValid = false;
      this.validateMessage = 'Selection contains different blocks';
      return;
    }

    nodes.splice(0, 1);

    let hasBadNodes = false;
    nodes.forEach((n) => {
      if (this._badSymbols(n.className.toLowerCase())) {
        hasBadNodes = true;
      }
    });

    if (hasBadNodes) {
      this.isValid = false;
      this.validateMessage = 'Selection contains different blocks';
    }
  }

  _checkTables(nodes, last) {
    if (last.className === 'MsoNormal' || last.className.toLowerCase().includes('list')) {
      this.isValid = false;
      this.validateMessage = 'Selection contains different blocks';
      return;
    }

    nodes.splice(0, 1);
    let hasBadNodes = false;
    nodes.forEach((n) => {
      if (this._badSymbols(n.className.toLowerCase())) {
        hasBadNodes = true;
      }
    });

    if (hasBadNodes) {
      this.isValid = false;
      this.validateMessage = 'Selection contains different blocks';
    }
  }

  _badSymbols(name) {
    if (name.includes('list')) {
      return true;
    }

    if (name.includes('table')) {
      return true;
    }

    return name.includes('normal');
  }

}

export default Validator;



