import { MathAlphabet } from '../utils/mathAlphabet';

export class Adapter {

  constructor() {
    this.fieldsKeys = [];
    this.fieldsList = [];
    this.alphabet = new MathAlphabet();
  }

  exec = async () => {
    await this.getFieldsKeys();
    await this.getFields();
    return this.fieldsList;
  };

  getFieldsKeys = async () => {
    await window.Word.run(async (context) => {
      const { body } = context.document;
      const searchResultsQuestions = body.search('[{]*[}]', { matchWildCards: true });
      context.load(searchResultsQuestions);
      await context.sync().then(async () => {
        searchResultsQuestions.items.forEach((item) => {
          const parseKey = item.text.slice(1, -1);
          const value = `{${this.getMathKey(parseKey)}}`;
          const hasStringKey = this.fieldsKeys.includes(item.text);
          const hasMathKey = this.fieldsKeys.includes(value);
          const { length } = parseKey;
          let isMath = false;
          for (let i = 0; i < length; i++) {
            if (this.alphabet.isMath(parseKey.codePointAt(i))) {
              isMath = true;
            }
          }

          if (isMath && !hasMathKey) {
            this.fieldsKeys.push(value);
          }

          if (!hasStringKey && !isMath) {
            this.fieldsKeys.push(item.text);
          }
        });

        await context.sync();
      });
    });
  };

  getFields = async () => {
    await window.Word.run(async (context) => {
      if (this.fieldsKeys.length) {
        this.fieldsKeys.forEach((key) => {
          const parseKey = key.slice(1, -1);
          const { length } = parseKey;
          let isMath = false;
          for (let i = 0; i < length; i++) {
            if (this.alphabet.isMath(parseKey.codePointAt(i))) {
              isMath = true;
            }
          }

          if (isMath) {
            const value = this.getMathKey(parseKey);
            const field = {
              title: value,
            };

            if (window.Office.context.document.settings.get(value.replace(/ /g, '_'))) {
              const options = window.Office.context.document.settings.get(value.replace(/ /g, '_'));
              if (options) {
                Object.assign(field, { type: options.Type, required: options.Required });
                this.fieldsList.push(field);
              }
            }
          } else {
            const field = {
              title: parseKey,
            };

            const options = window.Office.context.document.settings.get(parseKey.replace(/ /g, '_'));
            if (options) {
              Object.assign(field, { type: options.Type, required: options.Required });
              this.fieldsList.push(field);
            }
          }
        });
      }

      await context.sync();
    });
  };

  getMathKey(key) {
    this.alphabet.build();
    const { length } = key;
    let value = '';
    let i = 0;
    while (i < length) {
      value += this.alphabet.getSymbol(key.codePointAt(i)) ? this.alphabet.getSymbol(key.codePointAt(i)) : '';
      i++;
    }

    return value;
  }

}

export default Adapter;



