import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

import Dropdown from '../_components/dropdown';
import Field from '../_components/field';
import Button from '../_components/button';
import BackButton from '../_components/backButton';

import { Adapter } from './editField.adapter';
import { options, required, getOptions, getRequired } from './editField.options';

import s from './editField.style';

export class EditField extends PureComponent {

  static propTypes = {
    match: PropTypes.object.isRequired,
  };

  state = {
    field: {
      type: {},
      required: {},
      title: undefined,
    },
    defaultFieldTitle: null,
  };

  componentWillMount() {
    this.getOptions();
  }

  getOptions = () => {
    const adapter = new Adapter();
    adapter.getOptions(this.props.match.params.title).then(() => {
      const field = {
        type: options.get(adapter.field.type),
        required: required.get(adapter.field.required.toString()),
        title: adapter.field.title,
      };
      this.setState(prevState => ({
        ...prevState,
        field: {
          ...prevState.field,
          ...field,
        },
        defaultFieldTitle: field.title,
      }));
    });
  };

  handleEditField = () => {
    const adapter = new Adapter();
    adapter.editField(this.state.defaultFieldTitle, this.state.field).then(() => {
      this.setState(prevState => ({
        ...prevState,
        defaultFieldTitle: this.state.field.title,
      }));
    });
  };

  handleOptionChange = (option) => {
    const { id } = option;
    this.setState(prevState => ({
      field: {
        ...prevState.field,
        type: { ...options.get(id) },
      },
    }));
  };

  handleRequiredChange = (option) => {
    const { id } = option;
    this.setState(prevState => ({
      field: {
        ...prevState.field,
        required: required.get(id),
      },
    }));
  };

  handleTitleChange = (event) => {
    const title = event.target.value;
    this.setState(prevState => ({
      field: {
        ...prevState.field,
        title,
      },
    }));
  };

  handleTitleValidate = (event) => {
    const title = event.target.value;
    this.setState(prevState => ({
      field: {
        ...prevState.field,
        title: title || this.state.defaultFieldTitle,
      },
    }));
  };

  render() {
    return (
      <s.Root>
        <s.Header>
          <s.BackButton>
            <BackButton />
          </s.BackButton>
          <s.Title>Edit Field</s.Title>
        </s.Header>
        <s.ControlContainer>
          <Dropdown
            placeholder="Type"
            value={this.state.field.type.name}
            list={getOptions()}
            onChange={this.handleOptionChange}
          />
        </s.ControlContainer>
        <s.ControlContainer>
          <Dropdown
            placeholder="Required"
            value={this.state.field.required.name}
            list={getRequired()}
            onChange={this.handleRequiredChange}
          />
        </s.ControlContainer>
        <s.ControlContainer>
          <Field
            label="Title"
            value={this.state.field.title || ''}
            onBlur={this.handleTitleValidate}
            onChange={this.handleTitleChange}
          />
        </s.ControlContainer>
        <Button
          onClick={this.handleEditField}
          color="blue"
          fullWidth
        >
          Save
        </Button>
      </s.Root>
    );
  }

}

export default EditField;



