import styled from 'styled-components';

const style = {};

style.Root = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  height: 48px;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  background-color: ${props => props.theme.colors.brightOrange};
  z-index: 2;
`;

style.Text = styled.div`
  position: absolute;
  font-size: 14px;
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;
  color: ${props => props.theme.colors.white};
  z-index: 0;
`;

style.CloseButton = styled.div`
  position: relative;
  margin: 8px;
  width: 18px;
  height: 18px;
  display: flex;
  flex-shrink: 0;
  flex-grow: 0;
  align-items: center;
  justify-content: center;
  line-height: 18px;
  font-size: 14px;
  cursor: pointer;
  border-radius: 50%;
  border: 2px solid ${props => props.theme.colors.white};
  color: ${props => props.theme.colors.white};
  z-index: 1;
`;


export default style;



