import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

import list from './list.style';

class List extends PureComponent {

  static propTypes = {
    suggest: PropTypes.any.isRequired,
    itemSelected: PropTypes.string.isRequired,
    search: PropTypes.string,
  };

  static defaultProps = {
    search: null,
  };

  get suggest() {
    return this.props.suggest;
  }

  get items() {
    return this.suggest.state.items;
  }

  refsItem = new Map();

  scrollToItem(id) {
    const item = this.refsItem.get(id);
    if (item && item.scrollIntoView) {
      item.scrollIntoView({ block: 'nearest' });
    }
  }

  formatText(text) {
    const { search } = this.props;
    if (!search) {
      return text;
    }

    const regexp = this.suggest.createRegex(search, true);
    return text.replace(regexp, '<b>$1</b>');
  }

  handleItemMouseEnter = id => () => {
    this.suggest.setStateSkipTextBlur(true);
    this.suggest.setStateItemSelected(id);
  };

  handleItemMouseLeave = () => {
    this.suggest.setStateSkipTextBlur(false);
  };

  handleAddItemClick = () => {
    this.suggest.hideList();
    this.suggest.props.onNew(this.props.search);
  };

  handleItemClick = item => () => {
    const { suggest } = this;
    suggest.hideList();

    const prevItem = suggest.props.item;
    if (suggest.getId(prevItem) === suggest.getId(item)) {
      return;
    }

    suggest.props.onChange(item);
  };

  handleRefItem = id => (ref) => {
    if (ref) {
      this.refsItem.set(id, ref);
    } else {
      this.refsItem.delete(id);
    }
  };

  renderItem = (item) => {
    if (item.isAddItem) {
      return this.renderAddItem(item);
    }

    return this.renderDefaultItem(item);
  };

  renderDefaultItem(item) {
    const id = this.suggest.getId(item);
    const text = this.formatText(this.suggest.getValue(item));

    return (
      <list.Item
        key={id}
        selected={this.props.itemSelected === id}
        onClick={this.handleItemClick(item)}
        onMouseEnter={this.handleItemMouseEnter(id)}
        onMouseLeave={this.handleItemMouseLeave}
        innerRef={this.handleRefItem(id)}
        dangerouslySetInnerHTML={{ __html: text }} // eslint-disable-line react/no-danger
      />
    );
  }

  renderAddItem(item) {
    const id = this.suggest.getId(item);
    return (
      <list.Item
        key={id}
        selected={this.props.itemSelected === id}
        onClick={this.handleAddItemClick}
        onMouseEnter={this.handleItemMouseEnter(id)}
        onMouseLeave={this.handleItemMouseLeave}
        innerRef={this.handleRefItem(id)}
      >
        <list.AddItemLabel>Add Value </list.AddItemLabel>
        {this.props.search}
      </list.Item>
    );
  }

  render() {
    const { items } = this;
    if (!items.length) {
      return null;
    }

    return (
      <list.Root>
        {items.map(this.renderItem)}
      </list.Root>
    );
  }

}

export default List;



