import { MathAlphabet } from '../utils/mathAlphabet';

export class Adapter {

  constructor() {
    this.field = {};
    this.alphabet = new MathAlphabet();
  }

  getOptions = async (title) => {
    await window.Word.run(async (context) => {
      if (window.Office.context.document.settings.get(title)) {
        const options = window.Office.context.document.settings.get(title);
        if (options) {
          this.field = { type: options.Type, required: options.Required, title: options.Title };
        }
      }

      await context.sync();
    });
  };

  editField = async (dafaultTitle, newField) => {
    await this._findAndReplace(dafaultTitle, newField.title);
    await this._addSetting(dafaultTitle, newField);
  };

  _findAndReplace = async (dafaultTitle, title) => {
    await window.Word.run(async (context) => {
      const { body } = context.document;
      const searchResultsQuestions = body.search('[{]*[}]', { matchWildCards: true });
      context.load(searchResultsQuestions);
      await context.sync().then(async () => {
        searchResultsQuestions.items.forEach((item) => {
          const parse = item.text.slice(1, -1);

          const value = this._getMathValue(parse);
          if (dafaultTitle === parse || dafaultTitle === value) {
            item.insertText(`{${title}}`, window.Word.InsertLocation.replace);
            item.font.color = 'blue';
          }
        });

        await context.sync();
      });
    });
  };

  _getMathValue(title) {
    this.alphabet.build();
    const { length } = title;
    let value = '';
    let i = 0;
    while (i < length) {
      value += this.alphabet.getSymbol(title.codePointAt(i)) ? this.alphabet.getSymbol(title.codePointAt(i)) : '';
      i++;
    }

    return value;
  }

  _addSetting = async (oldField, field) => {
    const { type, required, title } = field;
    await window.Word.run(async (context) => {
      const name = title || type.id;
      const paramName = name.replace(/ /g, '_');
      const oldParam = oldField.replace(/ /g, '_');
      window.Office.context.document.settings.remove(oldParam);
      window.Office.context.document.settings.set(paramName, { Title: name, Type: type.id, Required: required.value });
      window.Office.context.document.settings.saveAsync();

      await context.sync();
    });
  };

}

export default Adapter;



