import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

import s from './titleEditor.style';

class TitleEditor extends PureComponent {

  static propTypes = {
    item: PropTypes.object.isRequired,
    onRename: PropTypes.func.isRequired,
    onBlur: PropTypes.func.isRequired,
  };

  state = {
    title: '',
  };

  componentWillMount() {
    this.readTitle();
  }

  componentDidMount() {
    this.refInput.focus();
    this.refInput.select();
  }

  rename() {
    const { item } = this.props;
    const title = this.state.title.trim();
    if (title === item) {
      this.props.onBlur();
      return;
    }

    if (title.length) {
      this.props.onRename(item, title);
    } else {
      this.readTitle();
      this.props.onBlur();
    }
  }

  readTitle() {
    this.setState({ title: this.props.item });
  }

  handleRefInput = (ref) => {
    this.refInput = ref;
  };

  handleChange = (event) => {
    const title = event.target.value;
    this.setState({ title });
  };

  handleClick = (event) => {
    event.stopPropagation();
    event.preventDefault();
  };

  handleKeyUp = (event) => {
    const isEnterKey = event.keyCode === 13;
    if (!isEnterKey) {
      return;
    }

    this.rename(event.target.value);
  };

  handleBlur = (event) => {
    this.rename(event.target.value);
  };

  render() {
    return (
      <s.Root
        type="text"
        maxLength="255"
        value={this.state.title}
        innerRef={this.handleRefInput}
        onChange={this.handleChange}
        onBlur={this.handleBlur}
        onKeyUp={this.handleKeyUp}
        onClick={this.handleClick}
      />
    );
  }

}

export default TitleEditor;



