import { Component } from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';

import Dom from '../../utils/dom';

class RenderToLayer extends Component {

  static propTypes = {
    componentClickAway: PropTypes.func,
    useLayerForClickAway: PropTypes.bool,
    children: PropTypes.node,
  };

  static defaultProps = {
    useLayerForClickAway: true,
    componentClickAway: () => {},
    children: null,
  };

  componentDidMount() {
    this.renderLayer();
  }

  componentWillUnmount() {
    this.unrenderLayer();
  }

  onClickAway = (event) => {
    if (event.defaultPrevented) {
      return;
    }

    if (!this.props.componentClickAway) {
      return;
    }

    const el = this.layer;

    if (
      (event.target !== el && event.target === window) ||
      (
        document.documentElement.contains(event.target) &&
        !Dom.isDescendant(el, event.target)
      )
    ) {
      this.props.componentClickAway(event);
    }
  };

  getLayer() {
    return this.layer;
  }

  layer = document.createElement('div');

  unrenderLayer() {
    if (!this.layer) {
      return;
    }

    if (this.props.useLayerForClickAway) {
      this.layer.style.position = 'relative';
      this.layer.removeEventListener('touchstart', this.onClickAway);
      this.layer.removeEventListener('click', this.onClickAway);
    } else {
      window.removeEventListener('touchstart', this.onClickAway);
      window.removeEventListener('click', this.onClickAway);
    }

    document.body.removeChild(this.layer);
  }

  renderLayer() {
    document.body.appendChild(this.layer);

    if (this.props.useLayerForClickAway) {
      this.layer.addEventListener('touchstart', this.onClickAway);
      this.layer.addEventListener('click', this.onClickAway);
      this.layer.style.position = 'fixed';
      this.layer.style.top = 0;
      this.layer.style.bottom = 0;
      this.layer.style.left = 0;
      this.layer.style.right = 0;
      this.layer.style.zIndex = 2000;
    } else {
      setTimeout(() => {
        window.addEventListener('touchstart', this.onClickAway);
        window.addEventListener('click', this.onClickAway);
      }, 0);
    }
  }

  render() {
    return ReactDOM.createPortal(this.props.children, this.layer);
  }

}

export default RenderToLayer;



