import styled from 'styled-components';

const style = {};

style.Root = styled.div`
   display: flex;
   flex-direction: column;
   padding: 16px 0;
   overflow: hidden;
   height: 100%;
`;

style.Header = styled.div`
  padding: 0 8px;
  display: flex;
  flex-direction: row;
`;

style.BackButton = styled.div`
  z-index: 1;
  position: absolute;
`;

style.Title = styled.div`
  font-size: 16px;
  color: ${props => props.theme.colors.dark};
  padding: 0 0 16px;
  flex: 1 0 auto;
  line-height: 24px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  z-index: 0;
  position: relative;
`;

style.Content = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
`;

export default style;



