import React, { PureComponent } from 'react';

import { Adapter } from './fields.adapter';
import { FieldItem as Item } from './item';
import BackButton from '../_components/backButton';

import s from './fields.style';

export class Fields extends PureComponent {

  state = {
    fields: [],
  };

  componentWillMount() {
    this.getFields();
  }

  getFields = () => {
    const adapter = new Adapter();
    adapter.exec().then((result) => {
      this.setState(() => ({
        fields: result,
      }));
    });
  };

  renderField = (field, index) => {
    const item = (
      <Item
        isFirst={index === 0}
        key={field.title}
        field={field}
      />
    );

    return item;
  };

  render() {
    return (
      <s.Root>
        <s.Header>
          <s.BackButton>
            <BackButton />
          </s.BackButton>
          <s.Title>Fields</s.Title>
        </s.Header>
        <s.Content>
          {this.state.fields.map(this.renderField)}
        </s.Content>
      </s.Root>
    );
  }

}

export default Fields;



