export class Adapter {

  constructor(field) {
    this.field = field;
  }

  addField = async () => {
    await this._addMarkup();
    await this._addSetting();
  };

  _addMarkup = async () => {
    const { title, type } = this.field;
    const insertString = ` {${title || type.id}} `;
    await window.Word.run(async (context) => {
      const range = context.document.getSelection();
      range.insertText(insertString, window.Word.InsertLocation.replace);
      const searchFieldMarkup = range.search('[{]*[}]', { matchWildCards: true });
      context.load(searchFieldMarkup);

      await context.sync().then(async () => {
        searchFieldMarkup.items[0].font.color = 'blue';
      });
    });
  };

  _addSetting = async () => {
    const { type, required, title } = this.field;
    await window.Word.run(async (context) => {
      const name = title || type.id;
      const paramName = name.replace(/ /g, '_');
      window.Office.context.document.settings.set(paramName, { Title: name, Type: type.id, Required: required.value });
      window.Office.context.document.settings.saveAsync();

      await context.sync();
    });
  };

}

export default Adapter;



