import styled, { css } from 'styled-components';

export const Text = styled.span`
  color: ${props => props.theme.colors.dark};
  font-size: 13px;
  text-transform: uppercase;
  
  ${props => !props.default && css`
    color: ${props.theme.colors.white};
  `}
`;

export const Button = styled.a`
  height: 40px;
  padding: 0 16px;
  border: 0;
  border-radius: 2px;
  background-color: ${props => props.theme.colors.whiteThree};
  outline: none;
  cursor: pointer;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &:focus { background-color: ${props => props.theme.colors.whiteTwo}; }
  &:hover { background-color: ${props => props.theme.colors.white}; }
  &:active { background-color: ${props => props.theme.colors.pinkishGrey}; }
  
  ${props => props.fullWidth && css`
    width: 100%;  
  `}
  
  ${props => props.large && css`
    height: 48px;
    padding: 0 24px;  
  `}
  
  ${props => props.green && css`
    background-color: ${props.theme.colors.green};
    
    &:focus { background-color: ${props.theme.colors.algae}; }
    &:hover { background-color: ${props.theme.colors.softGreen}; }
    &:active { background-color: ${props.theme.colors.lightForestGreen}; }
  `}
  
  ${props => props.blue && css`
    background-color: ${props.theme.colors.cornflowerBlue};
    
    &:focus { background-color: ${props.theme.colors.windowsBlue}; }
    &:hover { background-color: ${props.theme.colors.cornflowerBlueTwo}; }
    &:active { background-color: ${props.theme.colors.windowsBlueTwo}; }
  `}
  
  ${props => props.disabled && css`
    background-color: ${props.theme.colors.whiteThree};
    cursor: default;
  `}
`;



