import React, { PureComponent } from 'react';

import Notification from './notification';
import BackButton from '../_components/backButton';

import { Item } from './item';
import { Adapter } from './questions.adapter';

import s from './questions.style';

export class Questions extends PureComponent {

  state = {
    questions: [],
    answers: {},
    invalid: false,
    invalidMessage: '',
  };

  componentWillMount() {
    this.getQuestions();
  }

  getQuestions = () => {
    const adapter = new Adapter();
    adapter.getQuestions().then(() => {
      this.setState(() => ({
        questions: adapter.questions,
        answers: adapter.answers,
      }));
    });
  };

  handleSaveQuestion = (oldQuestion, newQuestion) => {
    const badSymbol = ':';
    const hasBadSymbol = newQuestion.includes(badSymbol);
    if (hasBadSymbol) {
      this.setState(prevState => ({
        ...prevState,
        invalid: true,
        invalidMessage: 'Question contains bad symbols',
      }));
    } else {
      const adapter = new Adapter();
      adapter.saveQuestion(oldQuestion, newQuestion).then(() => {
        this.getQuestions();
      });
    }
  };

  handleSaveAnswer = (question, oldAnswer, newAnswer) => {
    const badSymbol = ':';
    const hasBadSymbol = newAnswer.includes(badSymbol);
    if (hasBadSymbol) {
      this.setState(prevState => ({
        ...prevState,
        invalid: true,
        invalidMessage: 'Answer contains bad symbols',
      }));
    } else {
      const adapter = new Adapter();
      adapter.saveAnswer(question, oldAnswer, newAnswer).then(() => {
        this.getQuestions();
      });
    }
  };

  handleResetValidation = () => {
    this.setState(prevState => ({
      ...prevState,
      invalid: false,
      invalidMessage: '',
    }));
  };

  renderQuestions = (question) => {
    const answers = this.state.answers[question];
    const item = (
      <Item
        onSaveQuestion={this.handleSaveQuestion}
        onSaveAnswer={this.handleSaveAnswer}
        key={question}
        question={question}
        answers={answers}
      />
    );

    return item;
  };

  render() {
    return (
      <s.Root>
        <Notification
          show={this.state.invalid}
          message={this.state.invalidMessage}
          onClose={this.handleResetValidation}
        />
        <s.Header>
          <s.BackButton>
            <BackButton />
          </s.BackButton>
          <s.Title>Questions</s.Title>
        </s.Header>
        <s.Content>
          {this.state.questions.map(this.renderQuestions)}
        </s.Content>
      </s.Root>
    );
  }

}

export default Questions;



