import styled from 'styled-components';

const style = {};

style.Root = styled.div`
  display: flex;
  flex-direction: column;
  padding: 16px;
  overflow: hidden;
  height: 100%;
`;

style.Header = styled.div`
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
`;

style.Content = styled.div`
  flex-direction: column;
  flex: 1 0 0%;
  overflow: auto;
`;

style.Bottom = styled.div`
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
`;

style.BackButton = styled.div`
  z-index: 1;
  position: absolute;
`;

style.Title = styled.div`
  font-size: 16px;
  color: ${props => props.theme.colors.dark};
  padding: 0 0 16px;
  flex: 1 0 auto;
  line-height: 24px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  z-index: 0;
  position: relative;
`;

style.ControlContainer = styled.div`
  padding: 8px 0;
`;

export default style;



