import React, { Component } from 'react';
import PropTypes from 'prop-types';

import s from './field.style';

class Field extends Component {

  static propTypes = {
    disabled: PropTypes.bool,

    value: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.number,
    ]),

    defaultValue: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.number,
    ]),

    name: PropTypes.string,

    type: PropTypes.string,

    placeholder: PropTypes.string,

    id: PropTypes.string,

    label: PropTypes.string,

    error: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.bool,
    ]),

    inputRef: PropTypes.func,

    onChange: PropTypes.func,

    onFocus: PropTypes.func,

    onBlur: PropTypes.func,

    onKeydown: PropTypes.func,
  };

  static defaultProps = {
    disabled: false,

    value: undefined,

    defaultValue: undefined,

    name: undefined,

    type: 'text',

    placeholder: undefined,

    id: undefined,

    label: undefined,

    error: undefined,

    inputRef: undefined,

    onChange: undefined,

    onFocus: undefined,

    onBlur: undefined,

    onKeydown: () => {},
  };

  state = {
    focused: false,
    dirty: false,
  };

  componentWillMount() {
    if (this.isControlled()) {
      this.checkDirty(this.props);
    }
  }

  componentDidMount() {
    this.updateTooltipPosition();

    if (!this.isControlled()) {
      this.checkDirty(this.inputRef);
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.isControlled()) {
      this.checkDirty(nextProps);
    }
  }

  componentDidUpdate() {
    this.updateTooltipPosition();
  }

  handleFocus = (event) => {
    this.setState({ focused: true });
    if (this.props.onFocus) {
      this.props.onFocus(event);
    }
  };

  handleBlur = (event) => {
    this.setState({ focused: false });
    if (this.props.onBlur) {
      this.props.onBlur(event);
    }
  };

  handleChange = (event) => {
    if (!this.isControlled()) {
      this.checkDirty(this.inputRef);
    }

    if (this.props.onChange) {
      this.props.onChange(event);
    }
  };

  handleKeyDown = (event) => {
    this.props.onKeydown(event);
  };

  handleInputRef = (node) => {
    this.inputRef = node;
    if (this.props.inputRef) {
      this.props.inputRef(node);
    }
  };

  handleTooltipRef = (node) => {
    this.tooltipRef = node;
  };

  checkDirty(obj) {
    const hasValue = obj && obj.value && obj.value.toString().length > 0;
    this.setState({ dirty: hasValue });
  }

  isControlled() {
    return typeof this.props.value === 'string';
  }

  hasTooltip() {
    const { error } = this.props;
    const { focused } = this.state;
    return ((typeof error === 'string') && error.length && focused);
  }

  updateTooltipPosition() {
    if (!this.hasTooltip()) {
      return;
    }

    const inputWidth = this.inputRef.clientWidth;
    const tooltipWidth = this.tooltipRef.clientWidth;

    const left = (inputWidth / 2) - (tooltipWidth / 2);
    this.tooltipRef.style.left = `${left}px`;
  }

  renderLabel() {
    const {
      disabled,
      label,
      error,
    } = this.props;

    const {
      focused,
      dirty,
    } = this.state;

    return (
      <s.Label
        shrink={dirty || focused}
        disabled={disabled}
        error={error}
      >
        <s.LabelText>{label}</s.LabelText>
      </s.Label>
    );
  }

  renderTooltip() {
    if (!this.hasTooltip()) {
      return null;
    }

    const { error } = this.props;

    return (
      <s.Tooltip innerRef={this.handleTooltipRef}>
        <s.TooltipContainer>{error}</s.TooltipContainer>
      </s.Tooltip>
    );
  }

  render() {
    const {
      disabled,
      value,
      defaultValue,
      name,
      type,
      placeholder,
      id,
      label,
      error,
      onChange,
      onFocus,
      onBlur,
      ...other
    } = this.props;

    return (
      <s.Root title={label}>
        {this.renderTooltip()}
        {this.renderLabel()}

        <s.Input
          error={error}
          onFocus={this.handleFocus}
          onBlur={this.handleBlur}
          onChange={this.handleChange}
          onKeyDown={this.handleKeyDown}
          innerRef={this.handleInputRef}
          disabled={disabled}
          value={value}
          defaultValue={defaultValue}
          name={name}
          type={type}
          placeholder={placeholder}
          id={id}
          {...other}
        />
      </s.Root>
    );
  }

}

export default Field;



