import React, { PureComponent } from 'react';
import uuid from 'uuid';

import BackButton from '../_components/backButton';
import Button from '../_components/button';
import Notification from './notification';

import { Item } from './item';
import { Adapter } from './addCondition.adapter';
import { Validator } from '../utils/validator';

import s from './addCondition.style';

export class AddCondition extends PureComponent {

  constructor() {
    super();

    this.state = this.createInitialState();
  }

  componentWillMount() {
    this.getQuestions();
  }

  getQuestions = () => {
    const adapter = new Adapter();
    adapter.getQuestions().then(() => {
      this.setState(() => ({
        questions: adapter.questions,
      }));
    });
  };

  createNewCondition = () => {
    const newCondition = {
      id: uuid(),
      question: '',
      answer: '',
    };

    const condition = [...this.state.condition];
    condition.unshift(newCondition);
    this.setState(prevState => ({
      ...prevState,
      condition,
    }));
  };

  createInitialState = () => {
    const newCondition = {
      id: uuid(),
      question: '',
      answer: '',
    };

    const state = {
      condition: [],
      questions: {},
      invaild: false,
      invalidMessage: '',
    };

    state.condition.push(newCondition);

    return state;
  };

  changeCondition = (item) => {
    const condition = this.state.condition.map((c) => {
      if (c.id === item.id) {
        return { ...c, question: item.question, answer: item.answer };
      }

      return c;
    });

    this.setState(prevState => ({
      ...prevState,
      condition,
    }));
  };

  handleResetValidation = () => {
    this.setState(prevState => ({
      ...prevState,
      invaild: false,
      invalidMessage: '',
    }));
  };

  validateForm = () => {
    const allFilled = this.state.condition.every(value => value.question && value.answer);
    if (!allFilled) {
      this.setState(prevState => ({
        ...prevState,
        invaild: true,
        invalidMessage: 'Some condition contain empty values',
      }));

      return false;
    }

    const badSymbol = ':';
    const allQuestionsCorrect = this.state.condition.every(value => !value.question.includes(badSymbol));
    if (!allQuestionsCorrect) {
      this.setState(prevState => ({
        ...prevState,
        invaild: true,
        invalidMessage: 'Question contains bad symbols',
      }));

      return false;
    }

    const allAnswersCorrect = this.state.condition.every(value => !value.answer.includes(badSymbol));
    if (!allAnswersCorrect) {
      this.setState(prevState => ({
        ...prevState,
        invaild: true,
        invalidMessage: 'Answer contains bad symbols',
      }));

      return false;
    }

    return true;
  };

  handleSaveCondition = () => {
    const validator = new Validator();
    validator.validateCondition().then(() => {
      if (validator.canInsert) {
        if (!this.validateForm()) return;
        this.addCondition();
        this.getQuestions();
      } else {
        this.setState(prevState => ({
          ...prevState,
          invaild: true,
          invalidMessage: validator.message,
        }));
      }
    });
  };

  addCondition = () => {
    const adapter = new Adapter();
    adapter.addCondition(this.state.condition).then(() => {
      this.setState(() => ({
        ...this.createInitialState(),
      }));
    });
  };

  handleDeleteCondition = (id) => {
    const condition = this.state.condition.filter(c => c.id !== id);
    this.setState(prevState => ({
      ...prevState,
      condition,
    }));
  };

  renderItems = item => (
    <Item
      key={item.id}
      item={item}
      questions={this.state.questions}
      onDelete={this.handleDeleteCondition}
      onChange={this.changeCondition}
    />
  );

  render() {
    return (
      <s.Root>
        <Notification
          show={this.state.invaild}
          message={this.state.invalidMessage}
          onClose={this.handleResetValidation}
        />
        <s.Header>
          <s.BackButton>
            <BackButton />
          </s.BackButton>
          <s.Title>Add Condition</s.Title>
          <s.ControlContainer>
            <Button
              onClick={this.createNewCondition}
              color="blue"
              fullWidth
            >
              Add Condition
            </Button>
          </s.ControlContainer>
        </s.Header>
        <s.Content>
          {this.state.condition.map(this.renderItems)}
        </s.Content>
        <s.Bottom>
          <s.ControlContainer>
            <Button
              onClick={this.handleSaveCondition}
              color="blue"
              fullWidth
            >
              Save Condition
            </Button>
          </s.ControlContainer>
        </s.Bottom>
      </s.Root>
    );
  }

}

export default AddCondition;



