import { injectGlobal } from 'styled-components';

// eslint-disable-next-line no-unused-expressions
injectGlobal`
  * {
    box-sizing: border-box;
  }

  body {
    margin: 0;
    padding: 0;
    font-family: Roboto-Regular, sans-serif;
    -webkit-font-smoothing: antialiased;
  }
  
  html,
  body,
  #root {
    height: 100%;
  }
`;



