import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

import UIPopover from '../popover/uiPopover';
import {
  List,
  ListItem,
} from '../list';

import s from './dropdown.style';
import ArrowIcon from '../icons/arrowDown.svg';
import ArrowIconDisabled from '../icons/arrowDownGrey.svg';

class Dropdown extends PureComponent {

  static propTypes = {
    disableItemCapitalizing: PropTypes.bool,
    list: PropTypes.array,
    placeholder: PropTypes.string,
    onClose: PropTypes.func,
    onChange: PropTypes.func.isRequired,
    disabled: PropTypes.bool,
    value: PropTypes.string,
    noRightBorder: PropTypes.bool,
    noLeftBorder: PropTypes.bool,
    noTopBorder: PropTypes.bool,
  };

  static defaultProps = {
    disableItemCapitalizing: false,
    noRightBorder: false,
    noLeftBorder: false,
    noTopBorder: false,
    value: '',
    onClose: undefined,
    list: [],
    placeholder: '',
    disabled: false,
  };

  state = {
    showDropdown: false,
    activeItem: null,
  };

  close() {
    this.setState({
      showDropdown: false,
      anchorEl: null,
    });

    if (this.props.onClose) {
      this.props.onClose();
    }
  }

  handleRequestClose = () => {
    if (this.state.showDropdown) {
      this.close();
    }
  };

  handleShowPopover = () => {
    this.setState({
      showDropdown: true,
      anchorEl: this.refInitialContainer,
    });
  };

  handleHidePopoverFromChildren = () => {
    this.setState({
      showDropdown: false,
    });
  };

  handleClick = item => () => {
    this.props.onChange(item);

    this.setState({
      showDropdown: false,
    });
  };

  handleRefInitialContainer = (ref) => {
    this.refInitialContainer = ref;
  };

  handleItemMouseEnter = item => () => {
    this.setState({
      activeItem: item,
    });
  };

  handleItemMouseLeave = () => {
    this.setState({
      activeItem: null,
    });
  };

  isActiveItem = (item) => {
    const { value } = this.props;
    const { activeItem } = this.state;

    if (activeItem && item.id === activeItem.id) {
      return true;
    }

    if (!activeItem && item.name === value) {
      return true;
    }

    return false;
  };

  renderInitialComponent() {
    const {
      value,
      list,
      placeholder,
      disabled,
    } = this.props;

    return (
      <s.DropdownButton
        disabled={disabled}
        onClick={!disabled ? this.handleShowPopover : () => {}}
        onKeyPress={Function.prototype}
        innerRef={this.handleRefInitialContainer}
      >
        <s.DropdownInput>
          <s.DropdownPlaceholder
            disabled={disabled}
          >
            {placeholder}
          </s.DropdownPlaceholder>
          <s.DropdownValue>
            {value || list[0].name}
          </s.DropdownValue>
        </s.DropdownInput>
        <s.DropdownArrow>
          {disabled && (
            <img
              src={ArrowIconDisabled}
              alt="icon"
            />
          )}

          {!disabled && (
            <img
              src={ArrowIcon}
              alt="icon"
            />)}
        </s.DropdownArrow>
      </s.DropdownButton>
    );
  }

  render() {
    const {
      list,
      noRightBorder,
      noLeftBorder,
      noTopBorder,
    } = this.props;

    const {
      showDropdown,
      anchorEl,
    } = this.state;

    return (
      <s.DropdownWrapper
        active={showDropdown}
        noRightBorder={noRightBorder}
        noLeftBorder={noLeftBorder}
        noTopBorder={noTopBorder}
      >
        <UIPopover
          style={{
            width: '100%',
            height: '100%',
          }}
          popoverProps={{
            showing: showDropdown,
            anchorEl,
          }}
          useAnchorWidth
          useLayerForClickAway
          handleHidePopoverFromChildren={this.handleHidePopoverFromChildren}
          onRequestClose={this.handleRequestClose}
          initialLabel={this.renderInitialComponent()}
        >
          <s.DropdownListWrapper>
            <List>
              {
                list.map(item => (
                  <ListItem
                    key={item.id}
                  >
                    <s.ListItem
                      onMouseEnter={this.handleItemMouseEnter(item)}
                      onMouseLeave={this.handleItemMouseLeave}
                      disableItemCapitalizing={this.props.disableItemCapitalizing}
                      active={this.isActiveItem(item)}
                      onClick={this.handleClick(item)}
                    >
                      <s.ItemName>
                        {item.name}
                      </s.ItemName>
                    </s.ListItem>
                  </ListItem>
                ))
              }
            </List>
          </s.DropdownListWrapper>
        </UIPopover>
      </s.DropdownWrapper>
    );
  }

}

export default Dropdown;



