import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import EventListener from 'react-event-listener';
import throttle from 'lodash/throttle';
import IsolatedScroll from 'react-isolated-scroll';
import RenderToLayer from './renderPortal';

const styles = {
  root: {
    display: 'none',
  },
};

class Popover extends PureComponent {

  static defaultProps = {
    anchorEl: null,
    anchorOrigin: {
      vertical: 'bottom',
      horizontal: 'left',
    },
    autoCloseWhenOffScreen: true,
    canAutoPosition: true,
    onRequestClose: () => {},
    open: false,
    targetOrigin: {
      vertical: 'top',
      horizontal: 'left',
    },
    useLayerForClickAway: true,
    useAnchorWidth: false,
    children: null,
    style: {},
  };

  static propTypes = {
    targetOrigin: PropTypes.object,
    anchorOrigin: PropTypes.object,
    style: PropTypes.object,
    useAnchorWidth: PropTypes.bool,
    canAutoPosition: PropTypes.bool,
    onRequestClose: PropTypes.func,
    autoCloseWhenOffScreen: PropTypes.bool,
    open: PropTypes.bool,
    children: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.node,
    ]),
    useLayerForClickAway: PropTypes.bool,
    anchorEl: PropTypes.oneOfType([
      PropTypes.object,
      PropTypes.node,
    ]),
  };


  constructor(props, context) {
    super(props, context);
    this.handleResize = throttle(this.setToClose, 100);
    this.handleScroll = throttle(this.setToClose, 50);

    this.state = {
      open: props.open,
      width: 'auto',
    };
  }

  componentDidMount() {
    this.setPlacement();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.open === this.props.open) {
      return;
    }

    if (nextProps.open) {
      clearTimeout(this.timeout);
      this.timeout = null;
      this.anchorEl = nextProps.anchorEl || this.props.anchorEl;
      this.setState(() => ({
        open: true,
      }));
    } else {
      this.setState(() => ({
        open: false,
      }));
    }
  }

  componentDidUpdate() {
    this.setPlacement();
  }

  componentWillUnmount() {
    this.handleResize.cancel();
    this.handleScroll.cancel();

    if (this.timeout) {
      clearTimeout(this.timeout);
      this.timeout = null;
    }
  }

  setToClose = () => {
    this.setState(() => ({
      open: false,
    }));
  };

  getAnchorPosition(el) {
    if (!el) {
      el = this.el; // eslint-disable-line prefer-destructuring
    }

    const rect = el.getBoundingClientRect();

    const anchor = {
      top: rect.top,
      left: rect.left,
      width: el.offsetWidth,
      height: el.offsetHeight,
    };

    this.setState(() => ({
      width: anchor.width,
    }));

    anchor.right = rect.right || anchor.left + anchor.width;
    anchor.bottom = rect.bottom || anchor.top + anchor.height;
    anchor.middle = anchor.left + ((anchor.right - anchor.left) / 2);
    anchor.center = anchor.top + ((anchor.bottom - anchor.top) / 2);

    return anchor;
  }

  getTargetPosition(targetEl) {
    return {
      top: 0,
      center: targetEl.offsetHeight / 2,
      bottom: targetEl.offsetHeight,
      left: 0,
      middle: targetEl.offsetWidth / 2,
      right: targetEl.offsetWidth,
    };
  }

  setPlacement = (scrolling) => {
    if (!this.state.open) {
      return;
    }

    const layer = this.layer.getLayer();

    if (!layer) {
      return;
    }

    const targetEl = layer.children[0];

    if (!targetEl) {
      return;
    }

    const { targetOrigin, anchorOrigin } = this.props;
    const anchorEl = this.props.anchorEl || this.anchorEl;

    const anchor = this.getAnchorPosition(anchorEl);
    let target = this.getTargetPosition(targetEl);

    let targetPosition = {
      top: anchor[anchorOrigin.vertical] - target[targetOrigin.vertical],
      left: anchor[anchorOrigin.horizontal] - target[targetOrigin.horizontal],
    };

    if (scrolling && this.props.autoCloseWhenOffScreen) {
      this.autoCloseWhenOffScreen(anchor);
    }

    if (this.props.canAutoPosition) {
      target = this.getTargetPosition(targetEl); // update as height may have changed
      targetPosition = this.applyAutoPositionIfNeeded(anchor, target, targetOrigin, anchorOrigin, targetPosition);
    }

    targetEl.style.top = `${Math.max(0, targetPosition.top)}px`;
    targetEl.style.left = `${Math.max(0, targetPosition.left)}px`;
    targetEl.style.maxHeight = '428px';
  };

  getOverlapMode(anchor, target, median) {
    if ([anchor, target].indexOf(median) >= 0) return 'auto';
    if (anchor === target) return 'inclusive';
    return 'exclusive';
  }

  getPositions(anchor, target) {
    const a = { ...anchor };
    const t = { ...target };

    const positions = {
      x: ['left', 'right'].filter(p => p !== t.horizontal),
      y: ['top', 'bottom'].filter(p => p !== t.vertical),
    };

    const overlap = {
      x: this.getOverlapMode(a.horizontal, t.horizontal, 'middle'),
      y: this.getOverlapMode(a.vertical, t.vertical, 'center'),
    };

    positions.x.splice(overlap.x === 'auto' ? 0 : 1, 0, 'middle');
    positions.y.splice(overlap.y === 'auto' ? 0 : 1, 0, 'center');

    if (overlap.y !== 'auto') {
      a.vertical = a.vertical === 'top' ? 'bottom' : 'top';
      if (overlap.y === 'inclusive') {
        t.vertical = t.vertical;
      }
    }

    if (overlap.x !== 'auto') {
      a.horizontal = a.horizontal === 'left' ? 'right' : 'left';
      if (overlap.y === 'inclusive') {
        t.horizontal = t.horizontal;
      }
    }

    return {
      positions,
      anchorPos: a,
    };
  }

  autoCloseWhenOffScreen(anchorPosition) {
    if (anchorPosition.top < 0 ||
      anchorPosition.top > window.innerHeight ||
      anchorPosition.left < 0 ||
      anchorPosition.left > window.innerWidth) {
      this.requestClose('offScreen');
    }
  }

  timeout = null;

  requestClose(reason) {
    if (this.props.onRequestClose) {
      this.props.onRequestClose(reason);
    }
  }

  componentClickAway = (event) => {
    event.preventDefault();
    this.requestClose('clickAway');
  };

  applyAutoPositionIfNeeded(anchor, target, targetOrigin, anchorOrigin, targetPosition) {
    const { positions, anchorPos } = this.getPositions(anchorOrigin, targetOrigin);

    if (targetPosition.top < 0 || targetPosition.top + target.bottom > window.innerHeight) {
      let newTop = anchor[anchorPos.vertical] - target[positions.y[0]];

      if (newTop + target.bottom <= window.innerHeight) {
        targetPosition.top = Math.max(0, newTop);
      } else {
        newTop = anchor[anchorPos.vertical] - target[positions.y[1]];
        if (newTop + target.bottom <= window.innerHeight) {
          targetPosition.top = Math.max(0, newTop);
        }
      }
    }

    if (targetPosition.left < 0 || targetPosition.left + target.right > window.innerWidth) {
      const newLeft = anchor[anchorPos.horizontal] - target[positions.x[0]];

      targetPosition.left = Math.max(0, newLeft);
    }

    return targetPosition;
  }

  renderLayer = () => {
    const {
      anchorEl,
      anchorOrigin,
      autoCloseWhenOffScreen,
      canAutoPosition,
      children,
      onRequestClose,
      style,
      targetOrigin,
      useAnchorWidth,
      useLayerForClickAway,
      ...other
    } = this.props;

    let styleRoot = style;

    styleRoot = {
      position: 'fixed',
      zIndex: 2100,
      background: '#fff',
      boxShadow: '0 2px 8px 0 rgba(0, 0, 0, 0.15), 0 0 2px 0 rgba(0, 0, 0, 0.15)',
      width: useAnchorWidth ? this.state.width : 'auto',
      height: this.state.height,
      overflow: 'auto',
    };

    if (!this.state.open) {
      return null;
    }

    return (
      <IsolatedScroll
        style={Object.assign(styleRoot, style)}
        {...other}
      >
        {children}
      </IsolatedScroll>
    );
  };

  render() {
    const {
      props: {
        useLayerForClickAway,
        open,
      },
    } = this;


    return (
      <div ref={(el) => { this.el = el; }} style={styles.root}>
        <EventListener
          target="window"
          onScroll={this.handleScroll}
          onResize={this.handleResize}
        />
        {
          open &&
          <RenderToLayer
            ref={(el) => { this.layer = el; }}
            componentClickAway={this.componentClickAway}
            useLayerForClickAway={useLayerForClickAway}
          >
            {this.renderLayer()}
          </RenderToLayer>
        }
      </div>
    );
  }

}

export default Popover;



