import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { escapeRegExp } from 'lodash';

import Field from '../field';
import Popover from '../popover';
import List from './list';
import ArrowIcon from '../icons/arrowDown.svg';
import ArrowIconDisabled from '../icons/arrowDownGrey.svg';
import s from './suggest.style';

const key = {
  up: 38,
  down: 40,
  enter: 13,
  esc: 27,
  tab: 9,
};

class Suggest extends PureComponent {

  static propTypes = {
    item: PropTypes.string,
    items: PropTypes.array,
    disabled: PropTypes.bool,
    error: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.bool,
    ]),
    label: PropTypes.string,
    getKey: PropTypes.func,
    onNew: PropTypes.func,
    onFocus: PropTypes.func,
    onBlur: PropTypes.func,
    inputRef: PropTypes.func,
    onChange: PropTypes.func.isRequired,
    getSuggestionValue: PropTypes.func.isRequired,
  };

  static defaultProps = {
    item: null,
    items: [],
    disabled: false,
    error: false,
    label: undefined,
    getKey: undefined,
    inputRef: undefined,
    onFocus: undefined,
    onBlur: undefined,
    onNew: null,
  };

  state = {
    search: null,
    items: [],
    itemSelected: '',
    listOpen: false,
  };

  componentWillReceiveProps(nextProps) {
    const { item, items } = this.props;
    const { item: nextItem, items: nextItems } = nextProps;

    const shouldUpdate = (item !== nextItem) || (items !== nextItems);
    if (shouldUpdate) {
      this.setStateItems(nextItems);
    }
  }

  componentDidUpdate(prevProps, prevState) {
    const { listOpen } = this.state;
    const { listOpen: prevListOpen } = prevState;
    if (!prevListOpen && listOpen) {
      const { itemSelected } = this.state;
      this.refList.scrollToItem(itemSelected);
    }
  }

  setStateItems(items) {
    this.setState({ items });
  }

  setStateSearch(search) {
    this.setState({ search });
  }

  setStateItemSelected(itemSelected) {
    this.setState({ itemSelected });
  }

  setStateSkipTextBlur(skip) {
    this.setState({ skipTextBlur: skip });
  }

  getId(item) {
    if (!item) {
      return '';
    }

    const { getKey } = this.props;
    return getKey ? getKey(item) : item.id;
  }

  // TODO:  
  getValue(item) {
    return item ? this.props.getSuggestionValue(item) : '';
  }

  refRoot = null;

  refField = null;

  refList = null;

  // TODO:  
  ensureAddItem(items, value) {
    if (!this.props.onNew) {
      return items;
    }

    if (!value.trim()) {
      return items;
    }

    if (items.some(p => this.getValue(p) === value)) {
      return items;
    }

    const addItem = {
      id: 'addItem',
      isAddItem: true,
    };

    if (!items.length) {
      return [addItem];
    }

    return items.slice(-1)[0].isAddItem
      ? items
      : [...items, addItem];
  }

  showList() {
    const { items } = this.props;
    this.setStateItems(items);

    const { item } = this.props;
    const itemSelected = this.getId(item || items[0]);
    this.setStateItemSelected(itemSelected);
    this.setState({ listOpen: true });
  }

  hideList() {
    this.setStateItemSelected('');
    this.setStateSearch(null);
    this.setState({ listOpen: false });
  }

  searchItems(items, search = '') {
    if (!search) {
      return items;
    }

    const regexp = this.createRegex(search);
    return items.filter(p => p.match(regexp));
  }

  createRegex(search, global = false) {
    const pattern = escapeRegExp(search.trim());
    return new RegExp(`(${pattern})`, `${global ? 'g' : ''}i`);
  }

  handleTextFocus = (event) => {
    this.refField.select();
    this.showList();

    if (this.props.onFocus) {
      this.props.onFocus(event);
    }
  };

  handleTextBlur = (event) => {
    const { skipTextBlur } = this.state;
    if (skipTextBlur) {
      this.setStateSkipTextBlur(false);
      return;
    }

    this.hideList();

    if (this.props.onBlur) {
      this.props.onBlur(event);
    }

    const { itemSelected } = this.state;
    if (itemSelected === this.getId(this.props.item)) {
      return;
    }

    const { items } = this.state;
    const item = items.find(p => this.getId(p) === itemSelected);
    if (!item) {
      return;
    }

    item.isAddItem
      ? this.props.onNew(this.state.search)
      : this.props.onChange(item);
  };

  handleTextChange = (event) => {
    const { value } = event.target;
    this.setStateSearch(value);

    let { items } = this.props;
    items = this.searchItems(items, value);
    items = this.ensureAddItem(items, value);
    this.setStateItems(items);

    const { itemSelected } = this.state;
    if (items.some(p => this.getId(p) === itemSelected)) {
      return;
    }

    if (items.length) {
      this.setStateItemSelected(this.getId(items[0]));
    }
  };

  handleTextKeyDown = (event) => {
    const { keyCode } = event;
    if (keyCode === key.down) {
      this.handleTextKeyDownArrowDown(event);
      return;
    }

    if (keyCode === key.up) {
      this.handleTextKeyDownArrowUp(event);
      return;
    }

    if (keyCode === key.esc) {
      this.handleTextKeyDownEsc();
      return;
    }

    if (keyCode === key.enter) {
      this.handleTextKeyDownEnter();
      return;
    }

    if (keyCode === key.tab) {
      this.handleTextKeyDownTab();
    }
  };

  handleTextKeyDownArrowDown(event) {
    event.preventDefault();
    const { items } = this.state;
    if (!items.length) {
      return;
    }

    let nextItemSelected = '';
    const { itemSelected } = this.state;
    if (itemSelected) {
      const index = items.findIndex(p => this.getId(p) === itemSelected);
      const nextIndex = Math.min(index + 1, items.length - 1);
      const nextItem = items[nextIndex];
      nextItemSelected = this.getId(nextItem);
    } else {
      nextItemSelected = this.getId(items[0]);
    }

    this.setStateItemSelected(nextItemSelected);
    this.refList.scrollToItem(nextItemSelected);
  }

  handleTextKeyDownArrowUp(event) {
    event.preventDefault();
    const { items } = this.state;
    if (!items.length) {
      return;
    }

    let nextItemSelected = '';
    const { itemSelected } = this.state;
    if (itemSelected) {
      const index = items.findIndex(p => this.getId(p) === itemSelected);
      const nextIndex = Math.max(index - 1, 0);
      const nextItem = items[nextIndex];
      nextItemSelected = this.getId(nextItem);
    } else {
      nextItemSelected = this.getId(items.slice(-1)[0]);
    }

    this.setStateItemSelected(nextItemSelected);
    this.refList.scrollToItem(nextItemSelected);
  }

  handleTextKeyDownEsc() {
    this.hideList();
    this.setStateSkipTextBlur(true);
    this.setState({}, () => this.refField.blur());
  }

  handleTextKeyDownEnter() {
    this.setStateSkipTextBlur(false);
    this.setState({}, () => this.refField.blur());
  }

  handleTextKeyDownTab() {
    this.setStateSkipTextBlur(false);
  }

  handleButtonClick = () => {
    this.setStateSkipTextBlur(true);
    const { listOpen } = this.state;
    if (listOpen) {
      this.hideList();
    } else {
      this.refField.focus();
      this.refField.select();
    }
  };

  handleButtonKeyDown = (event) => {
    const { keyCode } = event;
    if (keyCode === key.enter) {
      this.refField.focus();
      this.refField.select();
    }
  };

  handleButtonMouseEnter = () => {
    this.setStateSkipTextBlur(true);
  };

  handleButtonMouseLeave = () => {
    this.setStateSkipTextBlur(false);
  };

  handleRefRoot = (ref) => {
    this.refRoot = ref;
  };

  handleRefField = (ref) => {
    this.refField = ref;

    if (this.props.inputRef) {
      this.props.inputRef(ref);
    }
  };

  handleRefList = (ref) => {
    this.refList = ref;
  };

  renderList() {
    return (
      <List
        itemSelected={this.state.itemSelected}
        search={this.state.search}
        suggest={this}
        ref={this.handleRefList}
      />
    );
  }

  renderPopover() {
    return (
      <Popover
        open={this.state.listOpen}
        anchorEl={this.refRoot}
        anchorOrigin={{
          horizontal: 'left',
          vertical: 'bottom',
        }}
        targetOrigin={{
          horizontal: 'left',
          vertical: 'top',
        }}
        useLayerForClickAway={false}
        useAnchorWidth
      >
        {this.renderList()}
      </Popover>
    );
  }

  renderButton() {
    const { disabled } = this.props;
    if (disabled) {
      return (
        <s.Button>
          <img
            src={ArrowIconDisabled}
            alt="icon"
          />
        </s.Button>
      );
    }

    return (
      <s.Button
        role="button"
        tabIndex="0"
        onClick={this.handleButtonClick}
        onKeyDown={this.handleButtonKeyDown}
        onMouseEnter={this.handleButtonMouseEnter}
        onMouseLeave={this.handleButtonMouseLeave}

      >
        <img
          src={ArrowIcon}
          alt="icon"
        />
      </s.Button>
    );
  }

  render() {
    const { search } = this.state;
    const value = typeof search === 'string'
      ? search
      : this.getValue(this.props.item);

    return (
      <s.Root innerRef={this.handleRefRoot}>
        <Field
          value={value}
          onFocus={this.handleTextFocus}
          onBlur={this.handleTextBlur}
          onChange={this.handleTextChange}
          onKeyDown={this.handleTextKeyDown}
          inputRef={this.handleRefField}
          disabled={this.props.disabled}
          error={this.props.error}
          label={this.props.label}
        />
        {this.renderButton()}
        {this.renderPopover()}
      </s.Root>
    );
  }

}

export default Suggest;



