import React, { PureComponent } from 'react';

import Dropdown from '../_components/dropdown';
import Field from '../_components/field';
import Button from '../_components/button';
import BackButton from '../_components/backButton';
import Notification from './notification';

import { Validator } from '../utils/validator';
import { Adapter } from './addField.adapter';
import { options, required, getOptions, getRequired } from './addField.options';

import s from './addField.style';

export class AddField extends PureComponent {

  state = {
    field: {
      type: options.get('text'),
      required: required.get('false'),
      title: '',
    },
    invalidSelection: false,
    invalidMessage: '',
  };

  handleAddField = () => {
    const validator = new Validator();
    validator.validateField().then(() => {
      if (validator.canInsert) {
        this.addField();
        this.setState(prevState => ({
          ...prevState,
          invalidSelection: false,
          invalidMessage: '',
        }));
      } else {
        this.setState(prevState => ({
          ...prevState,
          invalidSelection: true,
          invalidMessage: validator.message,
        }));
      }
    });
  };

  addField = () => {
    const adapter = new Adapter(this.state.field);
    adapter.addField().then(() => {
      this.setState(() => ({
        field: {
          type: options.get('text'),
          required: required.get('false'),
          title: '',
        },
      }));
    });
  };

  handleOptionChange = (option) => {
    const { id } = option;
    this.setState(prevState => ({
      field: {
        ...prevState.field,
        type: { ...options.get(id) },
      },
    }));
  };

  handleRequiredChange = (option) => {
    const { id } = option;
    this.setState(prevState => ({
      field: {
        ...prevState.field,
        required: required.get(id),
      },
    }));
  };

  handleTitleChange = (event) => {
    const title = event.target.value;
    this.setState(prevState => ({
      field: {
        ...prevState.field,
        title,
      },
    }));
  };

  handleResetValidation = () => {
    this.setState(prevState => ({
      ...prevState,
      invalidSelection: false,
      invalidMessage: '',
    }));
  };

  render() {
    return (
      <s.Root>
        <Notification
          show={this.state.invalidSelection}
          message={this.state.invalidMessage}
          onClose={this.handleResetValidation}
        />
        <s.Header>
          <s.BackButton>
            <BackButton />
          </s.BackButton>
          <s.Title>Add Field</s.Title>
        </s.Header>
        <s.ControlContainer>
          <Dropdown
            placeholder="Type"
            value={this.state.field.type.name}
            list={getOptions()}
            onChange={this.handleOptionChange}
          />
        </s.ControlContainer>
        <s.ControlContainer>
          <Dropdown
            placeholder="Required"
            value={this.state.field.required.name}
            list={getRequired()}
            onChange={this.handleRequiredChange}
          />
        </s.ControlContainer>
        <s.ControlContainer>
          <Field
            label="Title"
            value={this.state.field.title || this.state.field.type.id}
            onChange={this.handleTitleChange}
          />
        </s.ControlContainer>
        <Button
          onClick={this.handleAddField}
          color="blue"
          fullWidth
        >
          Add Field
        </Button>
      </s.Root>
    );
  }

}

export default AddField;



