import styled from 'styled-components';
import field from '../field/field.style';

const style = {};

style.Root = styled.div`
  position: relative;

  ${field.Input} {
    padding-right: 48px;
  }
`;

style.Input = styled.input`
  width: 100%;
`;

style.Button = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
`;

export default style;



