const optionDate = {
  id: 'date',
  name: 'date field',
};

const optionNumber = {
  id: 'number',
  name: 'number filed',
};

const optionText = {
  id: 'text',
  name: 'text field',
};

const requiredTrue = {
  id: 'true',
  name: 'true',
  value: true,
};

const requiredFalse = {
  id: 'false',
  name: 'false',
  value: false,
};

export const options = new Map([
  [optionDate.id, optionDate],
  [optionNumber.id, optionNumber],
  [optionText.id, optionText],
]);

export const required = new Map([
  [requiredTrue.id, requiredTrue],
  [requiredFalse.id, requiredFalse],
]);

export const getOptions = () => {
  const values = options.values();
  return Array.from(values);
};

export const getRequired = () => {
  const values = required.values();
  return Array.from(values);
};



