import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

import TitleEditor from './titleEditor';

import s from './item.style';

export class Item extends PureComponent {

  static propTypes = {
    question: PropTypes.string.isRequired,
    isFirst: PropTypes.bool.isRequired,
    answers: PropTypes.array.isRequired,
    onSaveQuestion: PropTypes.func.isRequired,
    onSaveAnswer: PropTypes.func.isRequired,
  };

  state = {
    editTitle: false,
    answerIndex: null,
  };

  handleChangeAnswer = index => () => {
    this.setState(() => ({
      answerIndex: index,
    }));
  };

  handleChangeQuestion = () => {
    this.setState(() => ({
      editTitle: true,
    }));
  };

  handleSaveQuestion = (oldQuestion, newQuestion) => {
    this.setState(() => ({
      editTitle: false,
    }));

    this.props.onSaveQuestion(oldQuestion, newQuestion);
  };

  handleSaveAnswer = (oldAnswer, newAnswer) => {
    this.setState(() => ({
      answerIndex: null,
    }));

    this.props.onSaveAnswer(this.props.question, oldAnswer, newAnswer);
  };

  handleBlurQuestion = () => {
    this.setState(() => ({
      editTitle: false,
    }));
  };

  handleBlurAnswer = () => {
    this.setState(() => ({
      answerIndex: null,
    }));
  };

  renderTitle = () => {
    if (!this.state.editTitle) {
      return (
        <s.Title
          onClick={this.handleChangeQuestion}
        >
          {this.props.question}
        </s.Title>
      );
    }

    return (
      <TitleEditor
        item={this.props.question}
        onRename={this.handleSaveQuestion}
        onBlur={this.handleBlurQuestion}
      />
    );
  };

  renderAnswers = (item, index) => {
    if (this.state.answerIndex === index) {
      return (
        <s.AnswerItem
          isFirst={index === 0}
        >
          <TitleEditor
            item={item}
            onRename={this.handleSaveAnswer}
            onBlur={this.handleBlurAnswer}
          />
        </s.AnswerItem>
      );
    }

    return (
      <s.AnswerItem
        isFirst={index === 0}
        onClick={this.handleChangeAnswer(index)}
      >
        {item}
      </s.AnswerItem>
    );
  };

  render() {
    return (
      <s.Root
        isFirst={this.props.isFirst}
      >
        {this.renderTitle()}
        <s.AnswersRoot>
          {this.props.answers.map(this.renderAnswers)}
        </s.AnswersRoot>
      </s.Root>
    );
  }

}

export default Item;



